@import "../../../../common/c1-styles/s1-sass/mixins";
@import "../../../../common/c1-styles/s1-sass/variables";

.container{
@include flexContainer(column, center, center);
  padding: 14px;
  background-color: $secondaryBackground;
  border-radius: 10px;
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.08);
  min-width: 260px;
  img{
    width: 160px;
    border-radius: 50%;
  }
  .userName{
    font-size: 1.6rem;
    margin: 20px 0 14px 0;
  }
  .statusContainer{
    font-size: 1.2rem;
    margin-bottom: 24px;
  }

}